.toDoList {
 margin: 4rem auto;
 padding: 2rem 3rem 3rem;
 max-width: 500px;
 background: #ff6666;
 color: #fff;
 box-shadow: -20px -20px 0px 0px rgba(100, 100, 100, 0.1);
}

.toDoList ul {
 margin-top: 2.6rem;
 list-style: none;
}

.toDoList h2 {
 font-weight: normal;
 font-size: 2.6rem;
 letter-spacing: 0.05em;
 border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

.toDoList h2 span {
 display: block;
 font-size: 0.8rem;
 margin-bottom: 0.7rem;
 margin-left: 3px;
 margin-top: 0.2rem;
}
