.newToDoForm {
 display: flex;
 flex-wrap: wrap;
}
.newToDoForm label {
 min-width: 100%;
 margin-bottom: 0.5rem;
 font-size: 1.3rem;
}
.newToDoForm input {
 padding: 0.5em;
 flex-grow: 1;
 border: none;
 background: #f7f1f1;
 font-size: initial;
 outline: none;
}

.newToDoForm button {
 padding: 0 1.3rem;
 border: none;
 background: #ff6666;
 color: white;
 text-transform: uppercase;
 font-weight: bold;
 border: 1px solid rgba(255, 255, 255, 0.3);
 margin-left: 5px;
 cursor: pointer;
 transition: background 0.2s ease-out;
 outline: none;
}

.newToDoForm button:hover {
 background: #ff5e5e;
}
